
import { defineComponent, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";

export default defineComponent({
  setup() {
    const config = api.configGet();

    const dataMV = reactive({
      data: [] as any,
      page: 1,
      pageSize: 12,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingMV").show();

      const hide = () => cash("#loadingMV").hide();
      const page = dataMV.page;
      const pageSize = dataMV.pageSize;
      const search = dataMV.search;
      const url = api.memberMateri + 'video/' +
        "?search=" +
        search +
        "&page=" +
        page +
        "&pageSize=" +
        pageSize;

      const hitApi = await api.hitApiGet(url, hide);
      const ds = hitApi.data.diagnostic;
      const rs = hitApi.data.response;

      dataMV.data = rs;
      dataMV.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);

    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataMV.page = p;
      getData();
    };

    const nextPage = () => {
      dataMV.page = dataMV.page + 1;
      getData();
    };

    const beforePage = () => {
      dataMV.page = dataMV.page - 1;
      getData();
    };

    const lastPage = () => {
      dataMV.page = dataMV.paginate.totalPages;
      getData();
    };

    return {
      getData,
      dataMV,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      config,
    };
  },
});
