
import { defineComponent, reactive } from "vue";
import { sk, tahapanBelajar, tryoutPemantapan, topDMember } from "./dashboard-member";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const role = api.getRole();
    // console.log("login-data ", $ck.getCookie('login-data'));

    const listDM = reactive({
      listTMember: [],
      listSK: [],
      listTB: [],
    });
    const config = api.configGet();
    // for get wiget top
    axios
      .get(api.memberWidgetTop , config)
      .then(async (result: any) => {
        const rs = result.data.response;
        // console.log('response get wtop ', rs);
        listDM.listTMember = rs;
        cash('#loadingWTopMember').hide();
      })
      .catch(async (error: any) => {
        cash('#loadingWTopMember').hide();
        // console.log("response get wtop err ", error.response);
        await gpf.handleEds(error);
      });

    // for get syarat dan ketentuan garansi
    axios
      .get(api.memberSK , config)
      .then(async (result: any) => {
        const rs = result.data.response;
        // console.log('response get sk ', rs);
        listDM.listSK = rs;
        cash('#loadingSK').hide();
      })
      .catch(async (error: any) => {
        cash('#loadingSK').hide();
        // console.log("response get sk err ", error.response);
        await gpf.handleEds(error);
      });

    // for get data tahapan belajar
    axios
      .get(api.memberTB , config)
      .then(async (result: any) => {
        const rs = result.data.response;
        // console.log('response get tb ', rs);
        listDM.listTB = rs;
        cash('#loadingTB').hide();
      })
      .catch(async (error: any) => {
        cash('#loadingTB').hide();
        // console.log("response get tb err ", error.response);
        await gpf.handleEds(error);
      });

    return {
      role,
      listDM,
    };
  },
  data() {
    return {
      listSK: sk,
      listTB: tahapanBelajar,
      listTP: tryoutPemantapan,
      listTMember: topDMember,
    }
  },
});
